import isFunction from 'lodash/isFunction'
import isPlainObject from 'lodash/isPlainObject'
import { normolizeValue, isValueType, isModelType, isModelSetType } from './util'

/**
 * Attribute
 * å±æ§ç±»
 */
class Attribute {
  /**
   * æé å½æ°
   * @param  {String} name    å±æ§å
   * @param  {Object} options å±æ§éé¡¹
   */
  constructor (name, options) {
    let attribute = options
    if (!isPlainObject(options)) {
      attribute = {
        type: options
      }
    }

    Object.assign(this, {
      name: name,
      type: String,
      default: undefined
    }, attribute)
  }

  /**
   * è®¡ç®é»è®¤å¼
   * @return {Any} é»è®¤å¼
   */
  getDefaultValue () {
    let defaultValue = this.default

    if (isFunction(defaultValue)) {
      defaultValue = defaultValue()
    }

    return defaultValue
  }

  /**
   * æ ååå±æ§å¼
   * @param  {Any} value å¼
   * @return {Any}       æ ååå¼
   */
  normolizeValue (value) {
    // å¦æä¸ºç©ºåä½¿ç¨é»è®¤å¼
    value = value === undefined ? this.getDefaultValue() : value

    // æ ååç±»åå¼
    value = normolizeValue(value, this.type)

    return value
  }

  /**
   * æ ååæ¥å£æ°æ®å±æ§å¼
   * @param  {Any} value å¼
   * @return {Any}       æ ååå¼
   */
  normolizeDataValue (value) {
    const Type = this.type

    // å¦æä¸ºç©ºåä½¿ç¨é»è®¤å¼
    if (value === undefined) {
      value = this.getDefaultValue()
      value = normolizeValue(value, Type)

    // å¦æä¸ºæ¨¡åç±»åä½¿ç¨ fromData è½¬æ¢
    } else if (isModelType(Type)) {
      value = Type.fromData(value)

    // å¦æä¸ºæ¨¡åç±»éååä½¿ç¨ fromData è½¬æ¢
    } else if (isModelSetType(Type)) {
      const ItemType = Type[0]
      value = ItemType.fromDataSet(value)

    // å¸¸è§ç±»åå±æ§
    } else {
      value = normolizeValue(value, Type)
    }

    return value
  }

  /**
   * æ¯å¦ä¸ºå¼ç±»å
   */
  isValueType () {
    return isValueType(this.type)
  }

  /**
   * æ¯å¦ä¸ºæ¨¡åç±»å
   */
  isModelType () {
    return isModelType(this.type)
  }

  isModelSetType () {
    return isModelSetType(this.type)
  }
}

export default Attribute
