import Model from './Model'

/**
 * å®ä¹æ¨¡å
 * @param  {String} name       æ¨¡ååç§°
 * @param  {Object} attributes æ¨¡åå®ä¹å¯¹è±¡
 * @return {Function}          æ¨¡åç±»
 */
Model.define = function (name, attributes) {
  const ModelClass = class extends Model {}
  ModelClass.init(name, attributes)
  return ModelClass
}

/**
 * å®è£æä»¶
 * @param  {Object} plugin      æä»¶å¯¹è±¡
 * @param  {Object} options     æä»¶éé¡¹
 */
Model.use = function (plugin, options) {
  const installedPlugins = (this._installedPlugins || (this._installedPlugins = []))
  if (installedPlugins.indexOf(plugin) > -1) {
    return Model
  }

  const args = Array.from(arguments).slice(1)
  args.unshift(Model)
  if (typeof plugin.install === 'function') {
    plugin.install.apply(plugin, args)
  } else if (typeof plugin === 'function') {
    plugin.apply(null, args)
  }
  installedPlugins.push(plugin)
  return Model
}

// export
export default Model
