import getValue from 'lodash/get'
import setValue from 'lodash/set'
import mapValues from 'lodash/mapValues'
import Attribute from './Attribute'

/**
 * Model
 * æ¨¡åç±»
 */
class Model {
  /**
   * æ¨¡åæé å½æ°
   * @param  {Object} values æ¨¡åå±æ§æ°æ®
   * @return {[type]}        [description]
   */
  constructor (values = {}) {
    // å¯¹è±¡çå®éå¼
    this._initValues(values)
  }

  /**
   * åå§åæ¨¡åæ°æ®
   * @param  {Object} values æ¨¡åå±æ§æ°æ®
   */
  _initValues (values = {}) {
    const attributes = this.constructor.attributes
    mapValues(attributes, (attribute, name) => {
      let value = values[name]
      value = attribute.normolizeValue(values[name])
      this.set(name, value)
    })
  }

  /**
   * è·åæ¨¡åå±æ§å¼
   * @param  {String} name å±æ§åç§°
   * @return {Any}         å±æ§å¼
   */
  get (name) {
    return this[name]
  }

  /**
   * è®¾ç½®æ¨¡åå±æ§å¼
   * @param {String} name  å±æ§åç§°
   * @param {Any} value    å±æ§å¼
   */
  set (name, value) {
    if (!name) throw new Error('name åæ°ä¸è½ä¸ºç©ºï¼')
    this[name] = value
  }

  /**
   * åéå½åæ¨¡åå¯¹è±¡
   * @return {Object}  åéçæ°æ¨¡åå¯¹è±¡
   */
  clone () {
    return this.constructor.clone(this)
  }

  /**
   * ä»apiæ°æ®å¯¹è±¡è½¬æ¢ä¸ºæ¨¡åå¯¹è±¡
   * @param  {Object} data apiæ°æ®å¯¹è±¡
   * @return {Model}      æ¨¡åå¯¹è±¡
   */
  fromData (data = {}) {
    // æé¤ç©ºå¼åæ°ç»ç±»åï¼åç«¯å¯è½è¿åçé»è®¤ç©ºå¯¹è±¡ï¼
    if (!data || Array.isArray(data)) return this

    // æ å°å±æ§å¼
    const attributes = this.constructor.attributes
    mapValues(attributes, (attribute, name) => {
      let path = attribute.field || name
      let value = getValue(data, path)
      value = attribute.normolizeDataValue(value)
      this.set(name, value)
    })

    return this
  }

  /**
   * å°æ¨¡åå¯¹è±¡è½¬æ¢ä¸º apiæ°æ®å¯¹è±¡
   * @return {Object}  apiæ°æ®å¯¹è±¡
   */
  toData () {
    let data = {}
    const attributes = this.constructor.attributes
    mapValues(attributes, (attribute, name) => {
      // è·åå±æ§è®¿é®è·¯å¾åå¼
      let path = attribute.field || name
      let value = this.get(name)

      // æ¨¡åå¯¹è±¡ï¼è°ç¨æ¨¡åå¯¹è±¡ç toData æ¹æ³è·åå¼
      if (attribute.isModelType() && value) {
        value = value.toData()

      // æ¨¡åéåï¼è°ç¨æ¨¡åç toDataSet æ¹æ³è·åå¼
      } else if (attribute.isModelSetType()) {
        value = Model.toDataSet(value)
      }

      // è®¾ç½®å¼
      setValue(data, path, value)
    })

    return data
  }

  /**
   * åå»ºæ¨¡åå¯¹è±¡
   * @param  {Object} values æ¨¡åå±æ§å¼
   * @return {Model}        æ¨¡åå¯¹è±¡
   */
  static create (values) {
    if (Array.isArray(values)) {
      return this.bulkCreate(values)
    }

    return new this(values)
  }

  /**
   * åéæ¨¡åå¯¹è±¡
   * @param  {Object} model æ¨¡åå¯¹è±¡
   * @return {Model}        æ°æ¨¡åå¯¹è±¡
   */
  static clone (model) {
    return this.create(model)
  }

  /**
   * æ¹éåå»ºæ¨¡åå¯¹è±¡
   * @param  {Array<Object>}  valueSets æ¨¡åå±æ§å¼éå
   * @return {Array<Model>}             æ¨¡åéå
   */
  static bulkCreate (valueSets = []) {
    return valueSets.map((values) => this.create(values))
  }

  /**
   * ä»apiæ°æ®åå»ºæ¨¡åæ°æ®
   * @param  {Object} data apiæ°æ®
   * @return {Model}       æ¨¡åå¯¹è±¡
   */
  static fromData (data = {}) {
    if (Array.isArray(data)) {
      return this.fromDataSet(data)
    }

    return new this().fromData(data)
  }

  /**
   * å°æ¨¡åå¯¹è±¡è½¬æ¢ä¸º apiæ°æ®å¯¹è±¡
   * @param  {Model}  model æ¨¡åå¯¹è±¡
   * @return {Object}       apiæ°æ®å¯¹è±¡
   */
  static toData (model = {}) {
    if (Array.isArray(model)) {
      return this.toDataSet(model)
    }

    return model.$modelize ? model.toData() : new this(model).toData()
  }

  /**
   * ä»apiæ°æ®å¯¹è±¡éåæ¹éåå»ºæ¨¡åå¯¹è±¡éå
   * @param  {Array<Object>} data apiæ°æ®éå
   * @return {Array<Model>}       æ¨¡åå¯¹è±¡éå
   */
  static fromDataSet (dataSet = []) {
    return dataSet.map((data) => this.fromData(data))
  }

  /**
   * å°æ¨¡åå¯¹è±¡éåæ¹éè½¬æ¢ä¸º apiæ°æ®å¯¹è±¡éå
   * @param  {Array<Model>} models æ¨¡åå¯¹è±¡éå
   * @return {Array<Object>}       apiæ°æ®éå
   */
  static toDataSet (models = []) {
    return models.map((model) => model.toData())
  }

  /**
   * åå§åæ¨¡åç±»
   * @param  {String} name       æ¨¡åç±»åç§°
   * @param  {[type]} attributes æ¨¡åå®ä¹å¯¹è±¡
   * @return {[type]}            æ¨¡åç±»
   */
  static init (name, attributes) {
    // å±æ§å®ä¹å¯¹è±¡
    this.attributes = mapValues(attributes, (attribute, name) => new Attribute(name, attribute))
    // å±æ§é»è®¤å¼å¯¹è±¡
    // this.defaults = mapValues(attributes, (attribute, name) => attribute.default)

    // å®ä¹æ¨¡åç±»å±æ§
    Object.defineProperties(this, {
      // æ¨¡ååç§°
      name: {
        value: name,
        configurable: false,
        enumerable: false,
        writable: false
      }
    })

    // å®ä¹æ¨¡åç±»ååå±æ§
    Object.defineProperties(this.prototype, {
      // æ¨¡ååå±æ§
      $modelize: {
        value: true,
        configurable: false,
        enumerable: false,
        writable: false
      }
    })

    return this
  }

  /**
   * è·åå±æ§å®ä¹å¯¹è±¡
   * @param  {String} name       å±æ§å
   * @return {Object}            å±æ§å®ä¹å¯¹è±¡
   */
  static getAttribute (name) {
    return this.attributes[name]
  }
}

// export
export default Model
